.section--products {
	padding-top: 121px;
	padding-bottom: 98px;
	background-color: $main-white;
	@media screen and (min-width: $tablet) {
		padding-top: 122px;
		padding-bottom: 112px;
	}
	@media screen and (min-width: $desktop) {
		padding-top: 127px;
		padding-bottom: 119px;
	}
}
.label--products {
	margin-bottom: 15px;
}
.title--products {
	margin-bottom: 145px;
	@media screen and (min-width: $tablet) {
		margin-bottom: 150px;
	}
	@media screen and (min-width: $desktop) {
		margin-bottom: 260px;
	}
}
.products {
	@media screen and (min-width: $tablet) {
		display: flex;
		position: relative;
		align-items: flex-start;
	}
}
.products__content {
	position: relative;
	height: 250px;
	overflow: hidden;
	margin-bottom: 30px;
}
.products__title {
	font-family: "Titan One", cursive;
	@include font(400, 20px, 1.15);
	letter-spacing: 0.04em;
	text-align: center;
	margin-bottom: 70px;
	color: $main-white;
	position: relative;
	&::after {
		content: "\2026";
		font-size: 45px;
		position: absolute;
		top: 65%;
		left: 38%;
	}
	@media screen and (min-width: $desktop) {
		@include font(400, 30px, 1.13);
	}
}

.products__text {
	margin-bottom: 30px;
	@include font(700, 14px, 1.52);
	letter-spacing: 0.04em;
	text-align: center;
	color: $main-white;
	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 85px;
	}
	@media screen and (min-width: $tablet) {
		line-height: 1.76;
		margin-bottom: 40px;
	}
	@media screen and (min-width: $desktop) {
		font-size: 16px;
		margin-bottom: 60px;
	}
}
#more {
	display: none;
}
.products__item {
  position: relative;
  border-radius: 24px;
  padding: 160px 40px 40px;

  &::before {
    content: '';

    position: absolute;
    display: flex;
    top: -21%;
    left: 0px;

    display: block;

    width: 100%;
    height: 252px;

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    @media screen and (min-width: $tablet) {
      top: -21%;
    }
    @media screen and (min-width: $desktop) {
      top: -31%;
    }
  }

  &:first-child {
    background-color: $sd-brand-color;
    &::before {      

      @media screen and (max-width: $tablet - 1px) {
        background-image: url(../images/m-product-1.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/m-product-1-2x.png);
        }
      }

      @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
        background-image: url(../images/t-product-1.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/t-product-1-2x.png);
        }
      }
      
      @media screen and (min-width: $desktop) {
          background-image: url('../images/d-product-1.png');
          @media screen and (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
            background-image: url(../images/d-product-1-2x.png);
          }
      }
       
    }
  
  
  }

  &:first-child .products__text {
    &::before {
      background: linear-gradient(transparent, $sd-brand-color);
    }
  }

  &:first-child.active .products__content {
    height: auto;
  }

  &:nth-child(2) {
    background-color: $background-brown;
    &::before {
      
      @media screen and (max-width: $tablet - 1px) {
        background-image: url(../images/m-product-2.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/m-product-2-2x.png);
        }
      }

      @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
        background-image: url(../images/t-product-2.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/t-product-2-2x.png);
        }
      }
      
      @media screen and (min-width: $desktop) {
          background-image: url(../images/d-product-2.png);
          @media screen and (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
            background-image: url(../images/d-product-2-2x.png);
          }
      }

      // background-image: url('../images/d-product-2.png');
      // @media screen and (min-device-pixel-ratio: 2),
      //   (min-resolution: 192dpi),
      //   (min-resolution: 2dppx) {
      //   background-image: url(../images/d-product-2-2x.png);
      // }
    }
  }

  &:nth-child(2) .products__text {
    &::before {
      background: linear-gradient(transparent, $background-brown);
    }
  }

  &:nth-child(2).active .products__content {
    height: auto;
  }

  &:last-child {
    background-color: $background-green;
    &::before {

      @media screen and (max-width: $tablet - 1px) {
        background-image: url(../images/m-product-3.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/m-product-3-2x.png);
        }
      }

      @media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
        background-image: url(../images/t-product-3.png);
        @media screen and (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) 
        {
          background-image: url(../images/t-product-3-2x.png);
        }
      }
      
      @media screen and (min-width: $desktop) {
          background-image: url(../images/d-product-3.png);
          @media screen and (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
            background-image: url(../images/d-product-3-2x.png);
          }
      }

      // background-image: url('../images/d-product-3.png');
      // @media screen and (min-device-pixel-ratio: 2),
      //   (min-resolution: 192dpi),
      //   (min-resolution: 2dppx) {
      //   background-image: url(../images/d-product-3-2x.png);
      // }
    }
  }

  &:last-child .products__text {
    &::before {
      background: linear-gradient(transparent, $background-green);
    }
  }

  &:last-child.active .products__content {
    height: auto;
  }

  @media screen and (min-width: $tablet) {
    &:not(:last-child) {
      margin-right: 20px;
    }
    // flex-basis: calc((100% - 108px) / 3);
    // margin-left: 20px;
    // margin-top: 20px;
  }

  @media screen and (min-width: $desktop) {
    &:not(:last-child) {
      margin-right: 25px;
    }
  }

  @media screen and (max-width: $tablet - 1px) {
    &:not(:last-child) {
      margin-bottom: 130px;
    }
  }

  @media screen and (max-width: $desktop - 1px) {
    max-width: 280px;
    margin: 0 auto;
  }
}
.products__item.active .products__text {
	height: auto;
}
.products__item.active {
	&::before {
		top: -19%;
		@media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
			top: -14%;
		}
		@media screen and (min-width: $desktop) {
			top: -26%;
		}
	}
}
.products__item.active .products__text::before {
	display: none;
}
.products__button {
	display: block;
	margin: 0 auto;
	border-radius: 50%;
	width: 40px;
	height: 40px;
	background-image: url("../images/arrow.jpg");
	background-position: center;
	border-color: transparent;
	cursor: pointer;
}
